import { ApiService } from './../../services/api.service';
import { BancoService } from './../../services/banco.service';
import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { ToastController, ModalController, LoadingController, AlertController, Platform } from '@ionic/angular';

import { Plugins, CameraResultType } from '@capacitor/core';
const { Camera } = Plugins;

@Component({
    selector: 'app-login',
    templateUrl: './login.page.html',
    styleUrls: ['./login.page.scss'],
})
export class LoginPage implements OnInit {
    url = "https://livro-visual.herokuapp.com/graphql";
    opts = {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: ""
    };
    public usuarioForm: FormGroup;
    public cadastroForm: FormGroup;

    formCadastro = false
    imagem = {
        baixa_resolucao: '',
        alta_resolucao: ''
    }

    nivel_acesso = 1;


    constructor(
        private bancoLocal: BancoService,
        public formBuilder: FormBuilder,
        public toastCtrl: ToastController,
        public modalCtrl: ModalController,
        public loadingCtrl: LoadingController,
        public alertCtrl: AlertController,
        public platform: Platform,
        private api: ApiService
    ) {
        this.usuarioForm = formBuilder.group({
            email: ['', Validators.compose([Validators.required, Validators.email])],
            senha: ['', Validators.required]
        })
        this.cadastroForm = formBuilder.group({
            nome: ['', Validators.compose([Validators.required, Validators.minLength(10)])],
            email: ['', Validators.compose([Validators.required, Validators.email])],
            senha: ['', Validators.compose([Validators.required, Validators.minLength(6)])],
            repetirSenha: ['', Validators.required]
        })
    }

    ngOnInit() {
    }

    ionViewDidEnter() {
    }

    async logar() {
        let loading = await this.loadingCtrl.create({ message: "Verificando credenciais..." })
        loading.present()
        let body = `
            query{
                logIn(
                    email: "${this.usuarioForm.value.email}",
                    password: "${this.usuarioForm.value.senha}"
                ){
                    id
                    email
                    name
                    nivel_acesso
                    imagem{
                        id
                        link
                    }
                }
            }
        `
        let retorno = await this.api.get(body)
        loading.dismiss()
        if (!retorno.errors && retorno.data.logIn) {
            await this.bancoLocal.set("user-credentials", retorno.data.logIn)
            let toast = await this.toastCtrl.create({ message: "Login realizado com sucesso", duration: 3000 })
            toast.present()
            this.dismiss()
        } else {
            let alerta = await this.alertCtrl.create({ header: "Erro", message: "Credenciais incorretas, verifique e tente novamente" })
            alerta.present()
        }
        console.log("retorno: ", retorno);

        // this.opts.body = body
        // await fetch(this.url, this.opts)
        //     // .then(res => res.json())
        //     .then((data) => {
        //         console.log("data: ", data);
        //     })
        // await this.bancoLocal.set("user-credentials", { usuario: this.usuarioForm.value.email, senha: this.usuarioForm.value.senha })
        // let toast = await this.toastCtrl.create({ message: "Login realizado com sucesso", duration: 3000 })
        // toast.present()
    }

    async cadastrar() {
        let loading = await this.loadingCtrl.create({ message: "Cadastrando..." })
        loading.present()

        let body = `
            mutation{
                signUp(
                    email: "${this.cadastroForm.value.email}",
                    password: "${this.cadastroForm.value.senha}",
                    name: "${this.cadastroForm.value.nome}",
                    baixa_resolucao: "${this.imagem.baixa_resolucao}",
                    nivel_acesso: ${this.nivel_acesso}
                ){
                    id
                }
            }
        `
        let retorno = await this.api.post({ query: body });
        loading.dismiss()
        if (retorno.data && !retorno.errors) {
            let toast = await this.toastCtrl.create({ message: "Cadastro realizado com sucesso", duration: 3000 })
            toast.present()
            this.alternarForm()
        } else {
            if (retorno.errors && retorno.errors[0].message == "validation") {
                let alerta = await this.alertCtrl.create({ header: "Erro", message: "E-mail já utilizado." })
                alerta.present()
            } else{
                let alerta = await this.alertCtrl.create({ header: "Ocorreu um erro", message: "Tente novamente, ou contate o suporte." })
                alerta.present()
            }
        }
    }
    async alternarForm() {
        this.formCadastro = !this.formCadastro
    }

    dismiss() {
        // using the injected ModalController this page
        // can "dismiss" itself and optionally pass back data
        this.modalCtrl.dismiss({
            'dismissed': true
        });
    }

    selecionarImagem() {
        if (this.platform.is('capacitor')) {
            this.abreImagemCapacitor()
        } else {
            var target = document.getElementById('imagePicker')
            target.click()
        }
    }
    abreArquivo(e) {
        var file = e.target.files[0];
        if (!file) { return; }
        var reader = new FileReader();
        reader.onload = (e) => {
            var image = new Image();
            image.src = <string>e.target['result'];
            image.onload = async () => {
                var canvas = document.createElement('canvas'),
                    ctx = canvas.getContext('2d');
                canvas.width = 175;
                canvas.height = 175;
                ctx.drawImage(image, 0, 0, 175, 175);

                var canvasOriginal = document.createElement('canvas'),
                    ctx2 = canvasOriginal.getContext('2d');
                canvasOriginal.width = image.naturalWidth;
                canvasOriginal.height = image.naturalHeight;

                ctx2.drawImage(image, 0, 0, canvasOriginal.width, canvasOriginal.height);
                this.imagem.baixa_resolucao = canvas.toDataURL("image/jpeg", 0.3);
            }
        };
        reader.readAsDataURL(file);
    }

    async abreImagemCapacitor() {
        const image = await Camera.getPhoto({
            quality: 90,
            allowEditing: false,
            resultType: CameraResultType.Base64
        });
        // image.webPath will contain a path that can be set as an image src. 
        // You can access the original file using image.path, which can be 
        // passed to the Filesystem API to read the raw data of the image, 
        // if desired (or pass resultType: CameraResultType.Base64 to getPhoto)
        var imageUrl = image.webPath;
        // Can be set to the src of an image now
        console.log("Imagem: ", image.base64String);
        var image2 = new Image();
        image2.src = 'data:image/jpeg;base64,' + image.base64String;
        image2.onload = async () => {
            var canvas = document.createElement('canvas'),
                ctx = canvas.getContext('2d');
            canvas.width = 175;
            canvas.height = 175;
            ctx.drawImage(image2, 0, 0, 175, 175);

            var canvasOriginal = document.createElement('canvas'),
                ctx2 = canvasOriginal.getContext('2d');
            canvasOriginal.width = image2.naturalWidth;
            canvasOriginal.height = image2.naturalHeight;

            ctx2.drawImage(image2, 0, 0, canvasOriginal.width, canvasOriginal.height);
            this.imagem.baixa_resolucao = canvas.toDataURL("image/jpeg", 0.3);
        }
        // this.usuario['imagem'].baixa_resolucao = 'data:image/jpeg;base64,' + image.base64String;
    }
}
