import { NgModule } from '@angular/core';
import { PreloadAllModules, RouterModule, Routes } from '@angular/router';

const routes: Routes = [
  {
    path: '',
    redirectTo: 'home',
    pathMatch: 'full'
  },
  {
    path: 'home',
    loadChildren: () => import('./home/home.module').then(m => m.HomePageModule)
  },
  {
    path: 'list',
    loadChildren: () => import('./list/list.module').then(m => m.ListPageModule)
  },
  { path: 'login', loadChildren: './pages/login/login.module#LoginPageModule' },
  { path: 'usuario', loadChildren: './pages/usuario/usuario.module#UsuarioPageModule' },
  { path: 'atividades/minhas-atividades', loadChildren: './pages/atividades/minhas-atividades/minhas-atividades.module#MinhasAtividadesPageModule' },
  { path: 'atividades/nova-atividade', loadChildren: './pages/atividades/nova-atividade/nova-atividade.module#NovaAtividadePageModule' },
  { path: 'atividades/responder-atividade/:id', loadChildren: './pages/atividades/responder-atividade/responder-atividade.module#ResponderAtividadePageModule' }
];

@NgModule({
  imports: [
    RouterModule.forRoot(routes, { preloadingStrategy: PreloadAllModules })
  ],
  exports: [RouterModule]
})
export class AppRoutingModule {}
