import { Component } from '@angular/core';

import { Platform, AlertController, ModalController } from '@ionic/angular';
import { SplashScreen } from '@ionic-native/splash-screen/ngx';
import { StatusBar } from '@ionic-native/status-bar/ngx';
import { BancoService } from './services/banco.service';
import { Router } from '@angular/router';
import { LoginPage } from '../app/pages/login/login.page'

@Component({
    selector: 'app-root',
    templateUrl: 'app.component.html',
    styleUrls: ['app.component.scss']
})
export class AppComponent {
    public appPages = [];

    constructor(
        public alertCtrl: AlertController,
        public modalCtrl: ModalController,
        public router: Router,
        private platform: Platform,
        private splashScreen: SplashScreen,
        private statusBar: StatusBar,
        private bancoLocal: BancoService
    ) {
        this.initializeApp();
    }

    initializeApp() {
        this.platform.ready().then(() => {
            this.statusBar.styleDefault();
            this.splashScreen.hide();
            this.buscarUsuario()
        });
    }

    async buscarUsuario() {
        return new Promise(async done => {
            let user = await this.bancoLocal.get("user-credentials")
            this.appPages = [
                {
                    title: 'Início',
                    url: '/home',
                    icon: 'home'
                }
            ]
            if (user) {
                this.appPages = this.appPages.concat([
                    {
                        title: 'Usuário',
                        url: '/usuario',
                        icon: 'person'
                    }, {
                        title: 'Minhas Atividades',
                        url: '/atividades/minhas-atividades',
                        icon: 'folder'
                    },
                    {
                        title: 'Sair',
                        url: '',
                        action: 'sair',
                        icon: 'exit'
                    }
                ])
            } else {
                this.appPages.push({
                    title: 'Logar',
                    url: '',
                    action: 'logar',
                    icon: 'person'
                })

            }
            done(user)
        })
    }

    async acaoMenu(acao) {
        if (acao == 'sair') {
            let alert = await this.alertCtrl.create({
                subHeader: "Deseja sair da sua conta?", buttons: [{
                    text: "Não"
                }, {
                    text: "Sim",
                    handler: async () => {
                        await this.bancoLocal.remove('user-credentials')
                        await this.buscarUsuario()
                        this.router.navigate(['/home']);
                    }
                }]
            })
            alert.present()
        } else if (acao == 'logar') {
            let modal = await this.modalCtrl.create({ component: LoginPage, cssClass: 'modal-pequeno' })
            modal.present()
            modal.onDidDismiss().then(async () => {
                let user = await this.buscarUsuario()
                if (user)
                    this.router.navigate(['/usuario']);
            })
        }
    }
}
